<template>
    <div id="slider" class="mui-slider">
        <div id="sliderSegmentedControl" class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
            <div class="mui-scroll">
                <a :class="['mui-control-item',item.id == 0 ? 'mui-active' : '']" v-for="item in cates" :key="item.id" @tap="handleClick(item.id)">
                    {{item.title}}
                </a>
               
            </div>
        </div>

    </div>
</template>
<script>
//1mui.min.js
import mui from '../../lib/mui/js/mui.min.js'
import api from '@/api/index.js'

export default {
    data() {
        return{
            cates:[]//
        }
    },
    created() {
        this.getAllCategory();
    },
    mounted () {
        //2
        mui('.mui-scroll-wrapper').scroll({
            deceleration: 0.0005 //flick 0.0006
        });
    },
    methods: {
        //
        getAllCategory() {
            api.getAllCategory().then(res => {
                console.log(res);
                if(res.data.status === 0) {
                    res.data.message.unshift({title:'',id:0})
                    this.cates = res.data.message;
                }
            })
        },
        //
        handleClick(cateId){
            this.$emit('bindTab',{"cateId":cateId})
        }
    },
}
</script>
<style lang="scss" scoped>
    *{
touch-action: pan-y;
}
</style>


